<template>
  <page-content>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="24">
          <a-col :md="6" :sm="12">
            <a-form-item label="模块">
              <a-select
                v-model="queryParams.module"
                placeholder="请选择"
                default-value="0"
                style="width: 200px"
              >
                <a-select-option value="">全部</a-select-option>
                <a-select-option v-for="(key, value) in modules" :value="value" :key="value">{{
                  key
                }}</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="12">
            <a-form-item label="等级">
              <a-select
                v-model="queryParams.level"
                placeholder="请选择"
                default-value="0"
                style="width: 100px"
              >
                <a-select-option value="">全部</a-select-option>
                <a-select-option value="info">常规</a-select-option>
                <a-select-option value="error">错误</a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="12" :sm="12">
            <a-form-item label="开始时间">
              <a-date-picker
                v-model="queryParams.startTime"
                show-time
                placeholder="选择开始时间"
                @change="onStartChange"
                @ok="onStartOk"
              />
            </a-form-item>
            <a-form-item label="截止时间">
              <a-date-picker
                v-model="queryParams.endTime"
                show-time
                placeholder="选择截止时间"
                @change="onEndChange"
                @ok="onEndOk"
              />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-form-item label="关键字">
              <a-input v-model="queryParams.keywords" placeholder="输入关键字" style="width: 250px" />
            </a-form-item>
          </a-col>
          <a-col :md="6" :sm="12">
            <a-button type="primary" @click="search">查询</a-button>
            <a-button style="margin-left: 8px" @click="resetSearchForm()">重置</a-button>
          </a-col>
        </a-row>
      </a-form>
    </div>
    <div class="table-operator">
      <div class="batch-control pull-left"></div>
    </div>
    <a-table
      :columns="columns"
      :data-source="logs"
      :pagination="false"
      :rowSelection="rowSelection"
      ref="table"
      :loading="loading"
      rowKey="id"
    >
      <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
      <span slot="date" slot-scope="date">{{ date | filterDate(date) }}</span>
      <div slot="message" slot-scope="message" :title="message">{{ message | setContent(message) }}</div>
    </a-table>
    <lz-pagination :page="paginator" @change="changePaginator" @showSizeChange="showSizeChange" :autoPush="false" />
  </page-content>
</template>

<script>
import { getSystemLogs, getSystemLogModules } from '@/api/log'
import Space from '@c/Space'
import LzPagination from '@c/LzPagination'
// import { getRouteByCode } from '@/libs/utils'
import PageContent from '@c/PageContent'
import moment from 'moment'
import PermissionButton from '@/components/PermissionButton.vue'
const defaultQueryParams = {
    keywords: '',
    startTime: '',
    endTime: '',
    module: '',
    level: '',
    offset: 1,
    limit: 10
}
export default {
    name: 'Index',
    scroll: true,
    components: {
        LzPagination,
        Space,
        PermissionButton,
        PageContent
    },
    data () {
        return {
            visible: false,
            confirmLoading: false,
            loading: false,
            queryParams: defaultQueryParams,
            selectedRowKeys: [],
            selectedRows: [],
            modules: [],
            columns: [
                {
                    title: 'ID',
                    dataIndex: 'id',
                    key: 'id'
                },
                {
                    title: '操作人',
                    dataIndex: 'username',
                    key: 'username'
                },
                {
                    title: '模块',
                    dataIndex: 'module',
                    key: 'module'
                },
                {
                    title: '操作类型',
                    dataIndex: 'action',
                    key: 'action',
                    ellipsis: true
                },
                {
                    title: '日志内容',
                    dataIndex: 'message',
                    key: 'message',
                    scopedSlots: { customRender: 'message' }
                },
                {
                    title: 'IP',
                    dataIndex: 'ip',
                    key: 'ip'
                },
                {
                    title: '创建时间',
                    dataIndex: 'createdTime',
                    key: 'createdTime',
                    ellipsis: true,
                    scopedSlots: { customRender: 'time' }
                }
            ],
            logs: [],
            paginator: null,
            hasEdit: false,
            hasDelete: false
        }
    },
    async mounted () {
        this.loadData()
        this.getSystemLogModules()
        // this.checkPermissions()
    },
    computed: {
        rowSelection () {
            return {
                selectedRowKeys: this.selectedRowKeys,
                onChange: this.onSelectChange
            }
        }
    },
    methods: {
        async getSystemLogModules () {
            this.modules = await getSystemLogModules()
        },
        onStartChange (value, dateString) {
            console.log('Selected Time: ', value)
            console.log('Formatted Selected Time: ', dateString)
        },
        onStartOk (value) {
            console.log('onOk: ', value)
        },
        onEndChange (value, dateString) {
            console.log('Selected Time: ', value)
            console.log('Formatted Selected Time: ', dateString)
        },
        onEndOk (value) {
            console.log('onOk: ', value)
        },
        handleCancel () {
            this.visible = false
        },
        resetSearchForm () {
            this.queryParams = defaultQueryParams
        },
        search () {
            this.loadData()
            this.resetPagination()
        },
        refresh (resetSearch = true) {
            this.loadData()
            if (resetSearch) {
                this.resetSearchForm()
            }
        },
        resetPagination () {
            this.queryParams.offset = 1
            this.queryParams.limit = 10
        },
        showSizeChange (current, perPage) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: current,
                limit: perPage
            })
            this.refresh()
        },
        changePaginator (page, pageSize) {
            this.queryParams = Object.assign(this.queryParams, {
                offset: page,
                limit: pageSize
            })
            this.refresh()
        },
        async loadData () {
            this.loading = true
            console.log('this.queryParams:', this.queryParams)
            const { logs, paginator } = await getSystemLogs(this.queryParams)
            this.logs = logs
            this.paginator = paginator
            this.loading = false
        },
        dateFormat (row, column) {
            var date = row[column.property]
            console.log('date:', date)
            if (!date) {
                return ''
            }
            return moment(date).format('YYYY-MM-DD HH:mm:ss')
        },
        onSelectChange (selectedRowKeys, selectedRows) {
            this.selectedRowKeys = selectedRowKeys
            this.selectedRows = selectedRows
        }
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        },
        setContent (value) {
            if (!value) return ''
            const len = 100
            if (value.length > len) {
                return value.slice(0, len) + '...'
            }
            return value
        }
    }
}
</script>
<style lang="less">
.del-span {
    text-decoration: line-through;
    color: red;
}
.pwd-border-none {
    .ant-input {
        border: none;
        padding-left: 0px;
        &:focus {
            border: none;
        }
    }
}
</style>
