<template>
  <div class="permission-button" v-if="!isCheck || currentBtnRoute">
    <div v-if="'tab' === type && routeLink">
      <router-link :to="routeLink">{{ title }}</router-link>
    </div>
    <div v-if="'modal' === type">
      <a-button
        :type="btnType"
        :icon="icon"
        :disabled="disabled"
        v-if="useBtnTag"
        @click="
          () => {
            $emit('handle-click')
          }
        "
        :loading="loading"
      >{{ title }}</a-button
      >
      <span
        v-else
        :class="classname ? classname : 'edit-btn'"
        @click="
          () => {
            $emit('handle-click')
          }
        "
      >
        {{ title }}</span
      >
    </div>
    <div v-if="'confirm' === type">
      <span
        :class="classname ? classname : 'del-btn'"
        @click="
          () => {
            $emit('handle-confirm')
          }
        "
      >
        {{ title }}</span
      >
    </div>
  </div>
</template>
<script>
import { getRouteByCode } from '@/libs/utils'
export default {
    name: 'PermissionButton',
    props: {
        routeLink: null,
        type: {
            type: String,
            default: 'modal'
        },
        code: {
            type: String,
            default: ''
        },
        title: {
            type: String,
            default: ''
        },
        loading: {
            type: Boolean,
            default: false
        },
        icon: {
            type: String,
            default: 'plus'
        },
        useBtnTag: {
            type: Boolean,
            default: true
        },
        disabled: {
            type: Boolean,
            default: false
        },
        classname: {
            type: String,
            default: ''
        },
        btnType: {
            type: String,
            default: 'primary'
        },
        isCheck: {
            type: Boolean,
            default: true
        },
        confirm: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
            currentBtnRoute: null
        }
    },
    mounted () {
        this.checkPermission()
    },
    methods: {
        checkPermission () {
            if (!this.isCheck) {
                return
            }
            const vueRouters = this.$store.state.vueRouters.vueRouters
            if (!this.code || !vueRouters) {
                return
            }

            this.currentBtnRoute = getRouteByCode(vueRouters, this.code)
        },
        handleClick (e) {
            this.$emit('bclick')
        }
    }
}
</script>

<style lang="less" scoped>
.del-btn,
.edit-btn {
    color: #1890ff;
    cursor: pointer;
}
</style>
